import { Injectable } from '@angular/core';
import * as Excel from "exceljs/dist/exceljs.min.js";
import * as FileSaver from 'file-saver';
import {
  reportHeaderForVinSummary, reportHeaderForPaymentType, reportHeaderStyleForVinSummary,
  reportHeaderStyleForPaymentStatus, reportHeaderForIncentiveType, reportHeaderStyleForIncentiveStatus,
  reportHeaderStyleForPaymentByMethod, reportHeaderForPaymentByMethod, reportHeaderStyleForPaymentByIncType,
  reportHeaderForPaymentByIncType, reportHeaderStyleForPaymentByMethodDetails, reportHeaderForPaymentByMethodDetail, reportHeaderStyleForPaymentByIncTypeDetails, reportHeaderForPaymentByIncDetails,
  reportStyleForChallengeSearch, reportHeaderForChallengeSearchDetails, reportHeaderForDealReviewSearchDetails, reportStyleForDealReviewSearch
} from './isp-excel-report';

const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const EXCEL_EXTENSION = '.xlsx';

@Injectable({
  providedIn: 'root'
})
export class ExcelExportService {

  private currentRow: number;
  constructor() { }

  generateExcel(header: any[][], json: any[], excelFileName: string, reportName: string): void {
    let options = {
      filename: './Vin-Summary.xlsx',
      useStyles: true,
      useSharedStrings: true
    };
    let workbook = new Excel.Workbook(options);
    let worksheet = workbook.addWorksheet(excelFileName);
    // worksheet.properties.defaultRowHeight = 25; //setting to default height because of uneven height
    let reportHeaderStyle;
    let currentRow: number;
    let totalColumns: number;
    if (reportName == "vinSummary") {
      totalColumns = reportHeaderForVinSummary.length;
      this.generateReportHeader(worksheet, header, reportHeaderForVinSummary);
      reportHeaderStyle = reportHeaderStyleForVinSummary;
    }
    else if (reportName == "paymentStatus") {
      totalColumns = reportHeaderForPaymentType.length;
      this.generateReportHeader(worksheet, header, reportHeaderForPaymentType);
      reportHeaderStyle = reportHeaderStyleForPaymentStatus;
    }
    else if (reportName == "incentiveStatus") {
      totalColumns = reportHeaderForIncentiveType.length;
      this.generateReportHeader(worksheet, header, reportHeaderForIncentiveType);
      reportHeaderStyle = reportHeaderStyleForIncentiveStatus;
    }
    else if (reportName == "paymentByMethod") {
      totalColumns = reportHeaderStyleForPaymentByMethod.length;
      this.generateReportHeader(worksheet, header, reportHeaderForPaymentByMethod);
      reportHeaderStyle = reportHeaderStyleForPaymentByMethod;
    }
    else if (reportName == 'paymentByIncentiveType') {
      totalColumns = reportHeaderStyleForPaymentByIncType.length;
      this.generateReportHeader(worksheet, header, reportHeaderForPaymentByIncType);
      reportHeaderStyle = reportHeaderStyleForPaymentByIncType;
    }
    else if (reportName == 'PaymentByPaymentMethodDetails') {
      totalColumns = reportHeaderStyleForPaymentByMethodDetails.length;
      this.generateReportHeader(worksheet, header, reportHeaderForPaymentByMethodDetail);
      reportHeaderStyle = reportHeaderStyleForPaymentByMethodDetails;
    }
    else if (reportName == 'paymentByIncentiveTypeDetails') {
      totalColumns = reportHeaderStyleForPaymentByIncTypeDetails.length;
      this.generateReportHeader(worksheet, header, reportHeaderForPaymentByIncDetails);
      reportHeaderStyle = reportHeaderStyleForPaymentByIncTypeDetails;
    }
    else if (reportName == 'ChallengeSummary') {
      totalColumns = reportHeaderForChallengeSearchDetails.length;
      this.generateReportHeader(worksheet, header, reportHeaderForChallengeSearchDetails);
      reportHeaderStyle = reportStyleForChallengeSearch;
    }
    else if (reportName == 'DealReviewSummary') {
      totalColumns = reportHeaderForDealReviewSearchDetails.length;
      this.generateReportHeader(worksheet, header, reportHeaderForDealReviewSearchDetails);
      reportHeaderStyle = reportStyleForDealReviewSearch;
    }
    worksheet.columns = reportHeaderStyle;
    let rowNum = this.currentRow;
    json.forEach(element => {

      worksheet.addRow(element);
      let reportData = worksheet.getRow(rowNum);
      for (let i = 1; i < (totalColumns) + 1; i++) {
        reportData.getCell(i).border = { top: { style: 'thin' }, left: { style: 'thin' }, bottom: { style: 'thin' }, right: { style: 'thin' } };
      }
      rowNum = rowNum + 1;
    });
    this.reportDetailFormat(worksheet, reportName);
    const excelBuffer: any = workbook.xlsx.writeBuffer();
    this.saveAsExcelFile(excelBuffer, excelFileName, workbook);
  }

  generateReportHeader(worksheet, header: any[][], reportHeader) {
    let rowNumber = 1;
    header.forEach(row => {
      worksheet.addRow(row);
      let reportHeaderRow = worksheet.getRow(rowNumber)
      reportHeaderRow.getCell(1).font = { name: 'Arial', size: 10, bold: true };
      reportHeaderRow.getCell(2).font = { name: 'Arial', size: 10, bold: false };
      //worksheet.getRow(rowNumber).width = 45; 
      rowNumber++;
    });
    this.reportHeaderFormat(worksheet, reportHeader, rowNumber);
  }


  private reportHeaderFormat(worksheet, reportHeader, row) {
    worksheet.getRow(row).values = reportHeader;
    let reportHeaderfill = worksheet.getRow(row);
    for (let i = 1; i < (reportHeader.length) + 1; i++) {
      reportHeaderfill.getCell(i).fill = {
        type: 'pattern',
        pattern: 'solid',
        fgColor: { argb: 'FFc0c0c0' },
        bgColor: { argb: 'FF0000FF' },
      }
      reportHeaderfill.getCell(i).border = { top: { style: 'thick' }, left: { style: 'thick' }, bottom: { style: 'thick' }, right: { style: 'thick' } }
      reportHeaderfill.getCell(i).font = { name: 'Arial', size: 10, bold: true };
    }
    this.currentRow = row + 1;
  }

  getCurrentDate(): string {
    let currentdate = new Date();
    let day = currentdate.getDate() < 10 ? "0" + currentdate.getDate() : currentdate.getDate();
    let month = currentdate.getMonth() + 1 < 10 ? "0" + (currentdate.getMonth() + 1) : (currentdate.getMonth() + 1);
    let datetime = "as on " + month + "/" + day + "/" + currentdate.getFullYear() + "  "
      + currentdate.getHours() + ":" + currentdate.getMinutes();
    return datetime;
  }


  private reportDetailFormat(worksheet, reportName) {
    worksheet.getRow(1).values = ' ';
    if (reportName == "vinSummary") {
      worksheet.getColumn(15).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(16).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';

    }
    else if (reportName == "paymentStatus") {
      worksheet.getColumn(22).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(23).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(24).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(25).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(26).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
    }
    else if (reportName == "incentiveStatus") {
      worksheet.getColumn(24).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(25).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
    }
    else if (reportName == "paymentByMethod") {
      worksheet.getColumn(4).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
    }
    else if (reportName == 'PaymentByPaymentMethodDetails') {
      worksheet.getColumn(4).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(11).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(12).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(13).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(14).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
    }
    else if (reportName == 'paymentByIncentiveType') {
      worksheet.getColumn(8).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(9).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(10).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(11).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(12).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(13).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
    }
    else if (reportName == 'paymentByIncentiveTypeDetails') {
      worksheet.getColumn(15).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(16).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(17).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(18).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
      worksheet.getColumn(19).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
    }
    else if (reportName == 'ChallengeSummary') {
      worksheet.getColumn(16).numFmt = '$#,##0.00;[Red]"($"#,##0.00")"';
    }

  }

  private saveAsExcelFile(buffer: any, fileName: string, workbook: any): void {
    workbook.xlsx.writeBuffer()
      .then(function (buffer) {
        // done buffering
        const data: Blob = new Blob([buffer], { type: EXCEL_TYPE });
        FileSaver.saveAs(data, fileName + EXCEL_EXTENSION);
      });

  }
}
