import { FilterParameter } from './vinSummarySearchRequest';

export class IncentiveDetailsRequest {
    sortData: any[];
    filterData: FilterParameter[];
    queryParams: IncentiveDetailQueryParameter;
    excelUrl: {
      url: string | null
  };
    pageNumber: number;
    taskToDo: string;
    totalResults: number;
    pageCnt: string;
    totalPages: number;
    currentPageNumber: number;
    recordsPerPage: number;
    // for controlling the number of data to be fetched
    showAll: boolean;

    constructor() {
      this.sortData = [];
      this.filterData = [];
      this.queryParams = new IncentiveDetailQueryParameter();
      this.excelUrl = {
        url:"excelExporter/searchPaymentsByTypeDetails"
      };
      this.pageNumber = 1;
      this.taskToDo = '';
      this.totalResults = null;
      this.showAll = false;
      this.pageCnt = '';
      this.totalPages = null;
      this.currentPageNumber = null;
      this.recordsPerPage = null;
    }
  }

  export class IncentiveDetailQueryParameter {
    dealerCode: string;
    incentiveTypeName: string;
    incentiveTypeNum: string;
    constructor() {
      this.dealerCode = '';
      this.incentiveTypeName = '';
      this.incentiveTypeNum = '';
    }
  }