import { BrowserModule } from '@angular/platform-browser';
import { APP_INITIALIZER, NgModule } from '@angular/core';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HeaderComponent } from './shared/header/header.component';
import { ISPAppConfigService } from './shared/services/isp-app-config.service';
import { IspHttpService } from './shared/services/isp-http.service';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { MessageHandlerComponent } from './shared/message-handler/message-handler.component';
import { MessageService } from './shared/message-handler/message-handler.service';
import { AgGridModule } from "ag-grid-angular";
import { CustomTextFilterComponent } from './shared/grid/filter/custom-text-filter.component';
import { ipLoaderComponent } from './shared/loader/ip-loader.component';
import { LoaderService } from './shared/loader/ip-loader.service';
import { DataSharingService } from './shared/services/data-sharing.service';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatCardModule } from '@angular/material/card';
import { MatTabsModule } from '@angular/material/tabs';
import { MatMenuModule } from '@angular/material/menu';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatRadioModule } from '@angular/material/radio';
import { MatInputModule } from '@angular/material/input';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatDividerModule } from '@angular/material/divider';
import { MatButtonModule } from '@angular/material/button';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatIconModule } from '@angular/material/icon';
import { MatNativeDateModule } from '@angular/material/core';
import { MatSelectModule, MatDialogModule } from '@angular/material'

import { VinSummaryComponent } from './vin-summary/vin-summary.component';
import { FooterComponent } from './shared/footer/footer.component';
import { PaymentByPaymentComponent } from './payment-by-payment/payment-by-payment.component';
import { PaymentByIncentiveComponent } from './payment-by-incentive/payment-by-incentive.component';
import { ChallengesComponent } from './challenges/challenges.component';
import { DealReviewsComponent } from './deal-reviews/deal-reviews.component';
import { VinSummaryDetailsComponent } from './vin-summary-details/vin-summary-details.component';
import { LoginComponent } from './login/login.component';
import { BreadcrumbComponent } from './shared/breadcrumb/breadcrumb.component';
import { CustomDropDownFilterComponent } from './shared/grid/filter/custom-drop-down-filter.component';
import { CustomDateFilterComponent } from './shared/grid/filter/custom-date-filter.component';
import { PageNavigateCellRendererComponent } from './shared/grid/page-navigate-cell-renderer/page-navigate-cell-renderer.component';
import { PrintDataGridService } from './shared/services/print-data-grid.service';
import { PaymentDetailsComponent } from './payment-details/payment-details.component';
import { IncentiveDetailsComponent } from './incentive-details/incentive-details.component';
import { ChallengesDetailComponent } from './challenges-detail/challenges-detail.component';
import { DealReviewDetailsComponent } from './deal-review-details/deal-review-details.component';
import { ConfirmModalComponent } from './shared/confirm-modal/confirm-modal.component';
import { ConfirmModalService } from './shared/confirm-modal/confirm-modal.service';
import { ReplaceZeroWithEmptyCellRendererComponent } from './shared/grid/replace-zero-with-empty-cell-renderer/replace-zero-with-empty-cell-renderer.component';
import { MdePopoverModule } from '@material-extended/mde';
import { HelpComponent } from './help/help.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { ContactUsComponent } from './contact-us/contact-us.component';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

export function configServiceFactory(ispAppConfig: ISPAppConfigService, loader: LoaderService) {
  return () => ispAppConfig.load();
}

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HeaderComponent,
    FooterComponent,
    ipLoaderComponent,
    MessageHandlerComponent,
    CustomTextFilterComponent,
    VinSummaryComponent,
    PaymentByPaymentComponent,
    PaymentByIncentiveComponent,
    ChallengesComponent,
    ChallengesDetailComponent,
    DealReviewsComponent,
    VinSummaryDetailsComponent,
    BreadcrumbComponent,
    CustomDropDownFilterComponent,
    CustomDateFilterComponent,
    PageNavigateCellRendererComponent,
    PaymentDetailsComponent,
    IncentiveDetailsComponent,
    DealReviewDetailsComponent,
    ConfirmModalComponent,
    ReplaceZeroWithEmptyCellRendererComponent,
    HelpComponent,
    AboutUsComponent,
    ContactUsComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    AgGridModule.withComponents([CustomTextFilterComponent, CustomDropDownFilterComponent, CustomDateFilterComponent, PageNavigateCellRendererComponent, ReplaceZeroWithEmptyCellRendererComponent]),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    BrowserAnimationsModule,
    MatCardModule,
    MatTabsModule,
    MatFormFieldModule,
    MatInputModule,
    MatRadioModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatToolbarModule,
    MatDividerModule,
    MatButtonModule,
    MatMenuModule,
    MatTooltipModule,
    MatIconModule,
    MatSelectModule,
    MatDialogModule,
    MdePopoverModule
  ],
  providers: [
    ISPAppConfigService,
    DataSharingService,
    IspHttpService,
    LoaderService,
    PrintDataGridService,
    { provide: APP_INITIALIZER, useFactory: configServiceFactory, deps: [ISPAppConfigService, LoaderService], multi: true },
    MessageService, ConfirmModalService
  ],
  entryComponents: [
    CustomTextFilterComponent, CustomDropDownFilterComponent, CustomDateFilterComponent, ConfirmModalComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
