import { Injectable } from '@angular/core';
import { IspHttpService } from './shared/services/isp-http.service';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { VSSearchRequest } from './shared/dataModels/vinSummarySearchRequest';
import { PaymentsSearchRequest } from './shared/dataModels/paymentsSearchRequest';
import { IncentiveSearchRequest } from './shared/dataModels/incentiveSearchRequest';
import { DealReviewSearchRequest } from './shared/dataModels/dealReviewSearchRequest';
import { PaymentDetailsRequest } from './shared/dataModels/paymentDetailsRequest';
import { IncentiveDetailsRequest } from './shared/dataModels/incentiveDetailsRequest';
import { ChallengesSearchRequest } from './shared/dataModels/challengeSearchRequest';
import { DRDetailsRequest } from './shared/dataModels/dealReviewDetailsRequest';
import { CreateChallengeDetailsRequest, DeleteChallengeDetailsRequest } from './shared/dataModels/createChallengeRequest';

@Injectable({
  providedIn: 'root'
})
export class IspAppService {
  private vinContext = environment.context + '/vinsummary';
  private paymentsContext = environment.context + '/payments';
  private challengeContext = environment.context + '/challenges';
  private challengeDetailContext = environment.context + '/challengeDetails';
  private dealReviewContext = environment.context + '/dealreview';
  private dealReviewDetailsContext = environment.context + '/dealReviewDetails';

  public VSDTabIndicator: number = 1;

  public vsSelectedData: VSSearchRequest = null;
  public selectedVIN: string = null;

  public incentiveSelectedData: IncentiveSearchRequest = null;
  public selectedIncentiveId: number = null;
  // Incentive detail tab, stores the selected vin
  public selectedVIN_IncDet: string = null;

  public paymentSelectedData: PaymentsSearchRequest = null;
  public selectedPayRefNo: string = null;
  // Incentive detail tab, stores the selected vin
  public selectedVIN_PayDet: string = null;

  public challengeSelectedData: ChallengesSearchRequest = new ChallengesSearchRequest();
  public challengeSelectedVIN: string = null;

  public dealReviewSelectedData: DealReviewSearchRequest = new DealReviewSearchRequest();
  public dealReviewSelectedVIN: string = null;

  public selectedFilterModel: any;
  public selectedSortModel: any;

  constructor(private IspHttpService: IspHttpService) { }

  /**
     @name vinSearch- call to db to fetch Vin Summary Grid Data.
     @param request - form details to update.
     */
  vinSearchForm(request: any): Observable<any> {
    return this.IspHttpService
      .post(`${this.vinContext}/searchSummary`, request)
      .pipe(
        (data: any) => {
          // this.logger.logMethodEnd('dist-fd', 'getDistFDDetails', 'Error in getDistFDDetails Service', '' + data);
          return data;
        }
      );
  }

  getVSDetails(request: any): Observable<any> {
    return this.IspHttpService
      .post(`${this.vinContext}/getIncentiveStatus`, request)
      .pipe(
        (data: any) => {
          // this.logger.logMethodEnd('dist-fd', 'getDistFDDetails', 'Error in getDistFDDetails Service', '' + data);
          return data;
        }
      );
  }

  getVSPaymentDetails(request: any): Observable<any> {
    return this.IspHttpService
      .post(`${this.vinContext}/getPaymentStatus`, request)
      .pipe(
        (data: any) => {
          // this.logger.logMethodEnd('dist-fd', 'getDistFDDetails', 'Error in getDistFDDetails Service', '' + data);
          return data;
        }
      );
  }

  // determines which tab is open in VIN Summary details, Incentive Status is 1 and Payment Status is 2
  setVSDTabIndicator(option: number) {
    this.VSDTabIndicator = option;
  }

  // stores the request object from the search form in the VIN Summary page, it's later used to preload the page when coming back to it via the breadcrumb
  setVSSelectedData(data: VSSearchRequest) {
    this.vsSelectedData = data;
  }

  // stores the VIN of the record selected in grid table, it's later used to preload the page when coming back to it via the breadcrumb
  setselectedVIN(vin: string) {
    this.selectedVIN = vin;
  }

  // stores the request object from the search form in the Payment detail page, it's later used to preload the page when coming back to it via the breadcrumb
  setPaymentSelectedData(data: PaymentsSearchRequest) {
    this.paymentSelectedData = data;
  }

  // stores the Payment Reference number of the record selected in any grid table, it's later used to preload the page when coming back to it via the breadcrumb
  setselectedPayRefNo(payRefNo: string) {
    this.selectedPayRefNo = payRefNo;
  }

  // stores the request object from the search form in the Incentive detail page, it's later used to preload the page when coming back to it via the breadcrumb
  setIncentiveSelectedData(data: IncentiveSearchRequest) {
    this.incentiveSelectedData = data;
  }

  // stores VIN in Incentive detail page
  setSelectedVIN_IncDet(vin: string) {
    this.selectedVIN_IncDet = vin;
  }

  // stores VIN in Incentive detail page
  setSelectedVIN_PayDet(vin: string) {
    this.selectedVIN_PayDet = vin;
  }
   
  // stores the request object from the search form in the Incentive detail page, it's later used to preload the page when coming back to it via the breadcrumb
  setselectedIncentiveId(data: number) {
    this.selectedIncentiveId = data;
  }

  setChallengeSelectedData(data: any) {
    this.challengeSelectedData = data;
  }

  setChallengeSelectedVIN(vin: string) {
    this.challengeSelectedVIN = vin;
  }

  setDealReviewSelectedData(data: DealReviewSearchRequest) {
    this.dealReviewSelectedData = data;
  }

  setDealReviewSelectedVIN(vin: string) {
    this.dealReviewSelectedVIN = vin;
  }

  // fetches the data for Payment by Payment Search Form
  fetchPayByPayData(request: PaymentsSearchRequest): Observable<any> {
    return this.IspHttpService.post(`${this.paymentsContext}/searchPaymentsByMethod`, request)
  }

  // fetches the data for Payment by Incentive Search Form
  fetchPayByIncData(request: IncentiveSearchRequest): Observable<any> {
    return this.IspHttpService.post(`${this.paymentsContext}/searchPaymentsByType`, request)
  }
  // fetches the data for paymentdetails
  getPaymentsDetails(request: PaymentDetailsRequest): Observable<any> {
    return this.IspHttpService.post(`${this.paymentsContext}/searchPaymentsByMethodDetails`, request).pipe(
      (data: any) => {
        return data;
      }
    );
  }
  // fetches the data for Incentivedetails
  getIncentiveDetails(request: IncentiveDetailsRequest): Observable<any> {
    return this.IspHttpService.post(`${this.paymentsContext}/searchPaymentsByTypeDetails`, request).pipe(
      (data: any) => {
        return data;
      }
    );
  }

  // fetches the data for challenges search form
  searchChallengeForm(request: ChallengesSearchRequest): Observable<any> {
    return this.IspHttpService.post(`${this.challengeContext}/searchChlngSummary`, request)
  }

  chlgdetailsSearchForm(request: any): Observable<any> {
    return this.IspHttpService.post(`${this.challengeDetailContext}/viewChallenge`, request)
  }

  createChallengeDetails(request: CreateChallengeDetailsRequest): Observable<any> {
    return this.IspHttpService.post(`${this.challengeDetailContext}/createChallenge`, request)
  }

  // fetches the data for Deal Review Search Form
  fetchDealReviewData(request: DealReviewSearchRequest): Observable<any> {
    return this.IspHttpService.post(`${this.dealReviewContext}/searchdealRvwSummary`, request)
  }

  getDRPaymentDetails(request: DRDetailsRequest): Observable<any> {
    return this.IspHttpService.post(`${this.dealReviewDetailsContext}/searchDealReviewById`, request)
  }

  deleteChallengeDetails(request: DeleteChallengeDetailsRequest): Observable<any> {
    return this.IspHttpService.post(`${this.challengeDetailContext}/deleteChallenge`, request)
  }

  getsaveDetails(request: any): Observable<any> {
    return this.IspHttpService.postForm(`${this.challengeDetailContext}/saveChallenge`, request)
      // .post(`${this.challengeDetailContext}/saveChallenge`, request)
      //.postForm('iplus-isp-0.0.1-SNAPSHOT/challengeDetails/saveChallenge', request)
      .pipe(
        (data: any) => {
          // this.logger.logMethodEnd('dist-fd', 'getDistFDDetails', 'Error in getDistFDDetails Service', '' + data);
          return data;
        }
      );
  }

  getDocument(request: any): Observable<any> {
    return this.IspHttpService.downloadUploadedFile(`${this.challengeDetailContext}/getDocument`, request)
      .pipe(
        (data: any) => {
          return data;
        }
      );
  }

  getDealDocument(request: any): Observable<any> {
    return this.IspHttpService.downloadUploadedFile(`${this.dealReviewDetailsContext}/retriveDocument`, request)
      .pipe(
        (data: any) => {
          return data;
        }
      );
  }

  submitChallengeDetails(request: any): Observable<any> {
    return this.IspHttpService.postForm(`${this.challengeDetailContext}/submitChallenge`, request)
      .pipe(
        (data: any) => {
          return data;
        }
      );
  }

  saveDealReview(request): Observable<any> {
    return this.IspHttpService.postForm(`${this.dealReviewDetailsContext}/saveDealReview`, request);
  }

  submitDealReview(request): Observable<any> {
    return this.IspHttpService.postForm(`${this.dealReviewDetailsContext}/submitDealReview`, request)
  }

  setFilterSortModel(filterModel: any, sortModel: any) {
    this.selectedFilterModel = filterModel;
    this.selectedSortModel = sortModel;
  }

}
