// request object used to fetch data in challenge page with form details
export class ChallengesSearchRequest {
    sortData: any[];
    filterData: FilterParameter[];
    queryParams: ChallengesQueryParameter;
    excelUrl: {
        url: string | null
    };
    pageNumber: number;
    taskToDo: string;
    totalResults: number;
    pageCnt: string;
    totalPages: number;
    currentPageNumber: number;
    recordsPerPage: number;
    // for controlling the number of data to be fetched
    showAll: boolean;

    // default values of few properties corrosponds to the pre-selected radio buttons on default page load
    constructor() {
        this.sortData = [];
        this.filterData = [];
        this.queryParams = new ChallengesQueryParameter();
        this.taskToDo = "searchByUpdates";
        this.excelUrl = {
            url: "excelExporter/searchChlngSummary"
        };
        this.pageNumber = null;
        this.totalResults = null;
        this.showAll = false;
        this.pageCnt = '';
        this.totalPages = null;
        this.currentPageNumber = null;
        this.recordsPerPage = null;
    }
}

// forms the base for object arrays required for the filterData property in the request object
export class FilterParameter {
    filterName: string;
    filterValue: string;
    constructor() {
        this.filterName = null;
        this.filterValue = null;
    }
}

// forms the query parameters for the above class in VIN Summary page
export class ChallengesQueryParameter {
    dealerCode: string;
    searchByRadio: string;
    vinSerial: string;
    status: string;
    serialNo: string;
    vin: string;
    constructor() {
        this.dealerCode = '';
        this.searchByRadio = '';
        this.vinSerial = '';
        this.status = '';
        this.serialNo = '';
        this.vin = '';
    }
    public replaceNonAscii(val) {
        return val.replace(/[^A-Za-z 0-9 \.,\?""!@#\$%\^&\*\(\)-_=\+;:<>\/\\\|\}\{\[\]`~]*/g, '');
    }
}