import { FilterParameter } from './vinSummarySearchRequest';

// In VIN Summary Details this request object is used for fetching the data in Incentive/Payment Status header panel tab
export class VSDetailsRequest {
  sortData: any[];
  filterData: FilterParameter[];
  queryParams: QueryParameter;
  isReadOnly: boolean;
  excelUrl: any;
  pageNumber: number;
  taskToDo: string;
  totalResults: number;
  // for controlling the number of data to be fetched
  showAll: boolean;
  constructor() {
    this.sortData = [];
    this.filterData = [];
    this.queryParams = new QueryParameter();
    this.isReadOnly = false;
    this.excelUrl = {
      url: "excelExporter/incentiveStatus"
    };
    this.pageNumber = 1;
    this.taskToDo = '';
  }
}

// Refers to the query parameters for the above object for Incentive/Payment Status header panel tab
export class QueryParameter {
  dealerCode: string;
  customerName: string;
  vin: string;
  retailDate: string;
  constructor() {
    this.dealerCode = '';
    this.customerName = '';
    this.vin = '';
    this.retailDate = '';
  }
}