import { Component, OnInit, Renderer2 } from '@angular/core';
import { DRDetailsRequest } from '../shared/dataModels/dealReviewDetailsRequest';
import { DRDetailsResultHeader } from '../shared/dataModels/dealReviewDetailsResponseHeader';
import { IspAppService } from '../isp-app.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ISPAppConfigService, IHeader } from '../shared/services/isp-app-config.service';
import { PrintDataGridService } from '../shared/services/print-data-grid.service';
import { ConfirmModalService } from '../shared/confirm-modal/confirm-modal.service';
import { MessageService } from '../shared/message-handler/message-handler.service';
import { saveAs as importedSaveAs } from 'file-saver';

@Component({
  selector: 'app-deal-review-details',
  templateUrl: './deal-review-details.component.html',
  styleUrls: ['./deal-review-details.component.scss']
})
export class DealReviewDetailsComponent implements OnInit {
  public requestObjectdr: DRDetailsRequest;
  public errorBar: boolean = true;
  public savemessage: boolean = false;
  public headerObj: IHeader;
  public drDetails: DRDetailsResultHeader;
  public drCreatedDate: Date;
  public drLastUpdateDate: Date;
  public drDispositionDate: Date;
  public drSubmitDate: Date;
  private Headersubscription: any;
  public uploadedDate: Date;
  public dealReviewSaved: boolean = false;
  public isPendingSubmit: boolean = false;
  public isReadOnly: boolean = false;
  public docList: any = [];
  public emptyFile: any;

  constructor(private printService: PrintDataGridService, private ispService: IspAppService, private renderer: Renderer2, private router: Router, private route: ActivatedRoute, private ispAppConfigService: ISPAppConfigService, private confirmModalServ: ConfirmModalService, private messageService: MessageService) {

    this.headerObj = this.ispAppConfigService.getHeaderDetails();
    this.drDetails = new DRDetailsResultHeader();
    this.emptyFile = new Blob([''], { type: 'application/octet-stream' });
  }


  ngOnInit() {
    this.renderer.addClass(document.body, 'print-details');
    this.setRequestObjectdr();
    this.Headersubscription = this.ispService.getDRPaymentDetails(this.requestObjectdr).subscribe(success => {
      this.drDetails = success;
      this.isPendingSubmit = !this.isReadOnly ? (this.drDetails.vehicleDetail.dealreviewStatus.toUpperCase() === 'RETURNED' || this.drDetails.vehicleDetail.dealreviewStatus.toUpperCase() === 'PENDING SUBMIT') : false;
      if (success.dealreviewRequestDate !== null) { this.drCreatedDate = new Date(success.dealreviewRequestDate); }
      if (success.dealreviewSubmitDate !== null) { this.drSubmitDate = new Date(success.dealreviewSubmitDate); }
      if (success.iagDealReviewLastUpdateDate !== null) { this.drLastUpdateDate = new Date(success.iagDealReviewLastUpdateDate); }
      if (success.dealReviewDispositionDate !== null) { this.drDispositionDate = new Date(success.dealReviewDispositionDate); }
      if (success.vehicleDetail.retailDate !== null) { this.drDetails.vehicleDetail.retailDt = success.vehicleDetail.retailDate; }

      for (let i = 0; i < success.requierdDocumentsDto.length; i++) {
        this.docList[i] = this.emptyFile;
        this.uploadedDate = success.requierdDocumentsDto[i].uploadedOn;
      }
    });
  }

  setRequestObjectdr() {
    this.requestObjectdr = new DRDetailsRequest();
    this.requestObjectdr.queryParams.dealerCode = this.headerObj.dealerCode;
    this.requestObjectdr.queryParams.dealReviewNo = this.route.snapshot.paramMap.get('dealReviewNo');

  }

  formRequest(): FormData {
    let formData = new FormData();
    formData.append('dealerCode', this.headerObj.dealerCode);
    formData.append('dealReviewNo', this.route.snapshot.paramMap.get('dealReviewNo'));
    formData.append('docReqStr', JSON.stringify(this.drDetails.requierdDocumentsDto));
    formData.append('commentLog',  JSON.stringify(this.drDetails.commentLog));
    formData.append('excel', '');
    formData.append('comment', this.drDetails.comments ? this.drDetails.comments : '');
    if (this.docList && this.docList.length) {
      this.docList.forEach(obj => {
        if (obj.size === 0 && obj.type === 'application/octet-stream') {
          formData.append('file', obj, '');
        }
        else {
          formData.append('file', obj);
        }
      });
    }
    return formData;
  }

  saveClick() {
    this.formRequest();
    this.ispService.saveDealReview(this.formRequest()).subscribe(success => {
      if (success.responseMessage === '1') {
        this.messageService.showMessage(success.error.errMessage, 'error');
      } else {
        this.drDetails = success;
        this.messageService.showMessage('Your updates have been saved', 'success');
      }

    });
  }

  submitClick() {
    let proceedFlag: boolean = true;
    // check if at least one document has been uploaded and it's extension is proper
    if (proceedFlag && this.drDetails.requierdDocumentsDto) {
      let uploadCounter: number = 0;
      this.drDetails.requierdDocumentsDto.forEach(element => {
        if (element.uploadedOn == null) {
          uploadCounter++;
        }
      })
      if (uploadCounter) {
        proceedFlag = false;
        this.messageService.showMessage('Must provide documents', 'error');
      }
    }
    if (proceedFlag) {
      this.messageService.hideMessage();
      this.confirmModalServ.showConfirm('No further changes will be allowed once submitted').afterClosed().subscribe(result => {
        if (result) {
          this.ispService.submitDealReview(this.formRequest()).subscribe(success => {
            this.drDetails = success;
            this.router.navigate(['/dealReviews']);
          })
        }
      });
    }
  }

  printDetails() {
    this.printService.printDetails('portrait');
  }

  uploadDocument(event, index) {
    let files = event.target.files;
    let file = files[0];
    const allowedFiles = ['.doc', '.docx', '.ppt', '.pptx', '.pptm', '.xls', '.xlsx', 'xlsm', '.pdf', '.zip', '.zipx', '.rar'];
    const regex = new RegExp("(" + allowedFiles.join('|') + ")$");
    if (files.length > 0) {
      if (!regex.test(file.name)) {
        this.messageService.showMessage('Please upload correct format file', 'error');
        setTimeout(() => {
          window.scrollTo(0, 0);
        }, 100);
      }
      else {
        if (file.size > 5242880) { // IPLUS-9729 - Increased file upload size to 5MB -5242880 bytes
          this.messageService.showMessage('Please upload file size less than 5MB', 'error');
          setTimeout(() => {
            window.scrollTo(0, 0);
          }, 100);
        }
        else {
          // Suppressing the error message when there's no error
          this.messageService.hideMessage();
          this.drDetails.requierdDocumentsDto[index].fileName = file.name;
          this.drDetails.requierdDocumentsDto[index].uploadedOn = new Date().getTime();
          this.docList[index] = (files[0]);
        }
      }
    }
  }

  getDocument(fName, id) {
    const formData: FormData = new FormData();
    formData.append('attachmentId', id);
    let name = [];
    name.push(fName.substring(0, fName.lastIndexOf('.')));
    name.push(fName.substring(fName.lastIndexOf('.') + 1));
    // const name = fName.split('.');
    const fileName = name[0] + '.' + name[1];
    this.ispService.getDealDocument(formData).subscribe(success => {
      if (success.responseMessage === '1') {
        this.messageService.showMessage(success.error.errMessage, 'error');
      } else {
        importedSaveAs(success, fileName);
      }

    })
  }


  deleteFile(index) {
    this.docList[index] = this.emptyFile;
    this.drDetails.requierdDocumentsDto[index].fileName = null;
    this.drDetails.requierdDocumentsDto[index].uploadedOn = null;
  }


  ngOnDestroy() {
    this.Headersubscription.unsubscribe();
    this.renderer.removeClass(document.body, 'print-details');
  }
}
