import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VinSummaryDetailsComponent } from './vin-summary-details/vin-summary-details.component';
import { VinSummaryComponent } from './vin-summary/vin-summary.component';
import { PaymentByIncentiveComponent } from './payment-by-incentive/payment-by-incentive.component';
import { ChallengesComponent } from './challenges/challenges.component';
import { DealReviewsComponent } from './deal-reviews/deal-reviews.component';
import { PaymentByPaymentComponent } from './payment-by-payment/payment-by-payment.component';
import { LoginComponent } from './login/login.component';
import { LoginGuard } from './login/login.guard';
import { VinSummaryGuard } from './vin-summary/vin-summary.guard';
import { PaymentByPaymentGuard } from './payment-by-payment/payment-by-payment.guard';
import { PaymentByIncentiveGuard } from './payment-by-incentive/payment-by-incentive.guard';
import { ChallengesGuard } from './challenges/challenges.guard';
import { DealReviewsGuard } from './deal-reviews/deal-reviews.guard';
import { VinSummaryDetailsGuard } from './vin-summary-details/vin-summary-details.guard';
import { PaymentDetailsComponent } from './payment-details/payment-details.component';
import { IncentiveDetailsComponent } from './incentive-details/incentive-details.component';
import { PaymentDetailsGuard } from './payment-details/payment-details.guard';
import { IncentiveDetailsGuard } from './incentive-details/incentive-details.guard';
import { ChallengesDetailComponent } from './challenges-detail/challenges-detail.component';
import { ChallengesDetailGuard } from './challenges-detail/challenges-detail.guard';
import { DealReviewDetailsComponent } from './deal-review-details/deal-review-details.component';
import { DealReviewDetailsGuard } from './deal-review-details/deal-review-details.guard';
import { HelpGuard } from './help/help.guard';
import { HelpComponent } from './help/help.component';


const routes: Routes = [
  {
    path: 'login',
    canActivate: [LoginGuard],
    component: LoginComponent
  },
  {
    path: 'vinSummary',
    canActivate: [VinSummaryGuard],
    component: VinSummaryComponent
  },
  {
    path: 'paymentByPayment',
    canActivate: [PaymentByPaymentGuard],
    component: PaymentByPaymentComponent
  },
  {
    path: 'paymentByPayment/paymentDetails/:paymentReferenceNo',
    canActivate: [PaymentDetailsGuard],
    component: PaymentDetailsComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Payments by Payment Method', route: '/paymentByPayment', append: '', urlParam: '' },
        { title: '', route: '', append: '', urlParam: 'paymentReferenceNo' }
      ]
    }
  },
  {
    path: 'paymentByPayment/vinSummaryDetails/:vin/:paymentReferenceNo/:user/:rDate',
    canActivate: [VinSummaryDetailsGuard],
    component: VinSummaryDetailsComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Payments by Payment Method', route: '/paymentByPayment', append: '', urlParam: '' },
        { title: '', route: 'paymentByPayment/paymentDetails/:paymentReferenceNo', append: '', urlParam: 'paymentReferenceNo' },
        { title: '', route: '', append: '', urlParam: 'vin' }
      ]
    }
  },
  {
    path: 'paymentByIncentive',
    canActivate: [PaymentByIncentiveGuard],
    component: PaymentByIncentiveComponent
  },
  {
    path: 'paymentByIncentive/incentiveDetails/:incentiveTypeName/:incentiveTypeNo',
    canActivate: [IncentiveDetailsGuard],
    component: IncentiveDetailsComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Payments by Incentive Type', route: '/paymentByIncentive', append: '', urlParam: '' },
        { title: '', route: '', append: '', urlParam: 'incentiveTypeName' }
      ]
    }
  },
  {
    path: 'paymentByIncentive/vinSummaryDetails/:vin/:incentiveTypeName/:incentiveTypeNo/:user/:rDate',
    canActivate: [VinSummaryDetailsGuard],
    component: VinSummaryDetailsComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Payments by Incentive Type', route: '/paymentByIncentive', append: '', urlParam: '' },
        { title: '', route: 'paymentByIncentive/incentiveDetails/:incentiveTypeName/:incentiveTypeNo', append: '', urlParam: 'incentiveTypeName' },
        { title: '', route: '', append: '', urlParam: 'vin' }
      ]
    }
  },
  {
    path: 'challenges',
    canActivate: [ChallengesGuard],
    component: ChallengesComponent
  },
  {
    path: 'challenges/detail/:vin/:challengeNo',
    canActivate: [ChallengesDetailGuard],
    component: ChallengesDetailComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Challenges', route: '/challenges', append: '', urlParam: '' },
        { title: 'VIN ', route: '', append: '', urlParam: 'vin' }
      ]
    }
  },
  {
    path: 'vin-summary/vinSummaryChallenge/:vin/Challenge/:user/:incentiveTypeNo/:optionType/:iaId/:rDate',
    canActivate: [ChallengesDetailGuard],
    component: ChallengesDetailComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'VIN Summary', route: '/vinSummary', append: '', urlParam: '' },
        { title: 'Details for ', route: 'vinSummary/vinSummaryDetails/:vin/:user/:rDate', append: '', urlParam: 'vin' },
        { title: 'Challenge', route: '', append: '', urlParam: '' }
      ]
    }
  },
  {
    path: 'paymentbypayment/paymentChallenge/:vin/:paymentReferenceNo/Challenge/:user/:incentiveTypeNo/:optionType/:iaId/:rDate',
    canActivate: [ChallengesDetailGuard],
    component: ChallengesDetailComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Payments by Payment Method', route: '/paymentByPayment', append: '', urlParam: '' },
        { title: '', route: 'paymentByPayment/paymentDetails/:paymentReferenceNo', append: '', urlParam: 'paymentReferenceNo' },
        { title: '', route: 'paymentByPayment/vinSummaryDetails/:vin/:paymentReferenceNo/:user/:rDate', append: '', urlParam: 'vin' },
        { title: 'Challenge', route: '', append: '', urlParam: '' }
      ]
    }
  },
  {
    path: 'paymentbyincentive/incentiveChallenge/:vin/:incentiveTypeName/:incentiveTypeNo/Challenge/:user/:incentiveTypeNo/:optionType/:iaId/:rDate',
    canActivate: [ChallengesDetailGuard],
    component: ChallengesDetailComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Payments by Incentive Type', route: '/paymentByIncentive', append: '', urlParam: '' },
        { title: '', route: 'paymentByIncentive/incentiveDetails/:incentiveTypeName/:incentiveTypeNo', append: '', urlParam: 'incentiveTypeName' },
        { title: '', route: 'paymentByIncentive/vinSummaryDetails/:vin/:incentiveTypeName/:incentiveTypeNo/:user/:rDate', append: '', urlParam: 'vin' },
        { title: 'Challenge', route: '', append: '', urlParam: '' }
      ]
    }
  },
  {
    path: 'dealReviews',
    canActivate: [DealReviewsGuard],
    component: DealReviewsComponent
  },
  {
    path: 'dealReviews/detail/:vin/:dealReviewNo',
    canActivate: [DealReviewDetailsGuard],
    component: DealReviewDetailsComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'Deal Reviews', route: '/dealReviews', append: '', urlParam: '' },
        { title: 'VIN ', route: '', append: '', urlParam: 'vin' }
      ]
    }
  },
  {
    path: 'vinSummary/vinSummaryDetails/:vin/:user/:rDate',
    canActivate: [VinSummaryDetailsGuard],
    component: VinSummaryDetailsComponent,
    data: {
      // data will be fetched from the respective paraMap and appended at the appropiate place
      breadcrumbs: [
        { title: 'VIN Summary', route: '/vinSummary', append: '', urlParam: '' },
        { title: `Details for VIN `, route: '', append: '', urlParam: 'vin' }
      ]
    }
  },
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
{
  path: 'help',
  canActivate: [HelpGuard],
  component: HelpComponent
}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }