import { Component, OnInit, Renderer2 } from '@angular/core';

import {TranslateService} from '@ngx-translate/core';
import {NgForm} from '@angular/forms';
import { RouterModule, Router, ActivatedRoute} from '@angular/router';
import { ISPAppConfigService } from '../shared/services/isp-app-config.service';
import { DataSharingService } from '../shared/services/data-sharing.service';



@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})

export class LoginComponent implements OnInit {
    //pageTitle:  string = 'Policy Managment System';
    public _dealerCode: string;
    private _ispDealerName: string;
    private _dealerName: string;
    private _dealerRegionName: string;
    private _dealerTheme: string;

  public get dealerTheme(): string {
    return this._dealerTheme;
  }
  public set dealerTheme(value: string) {
    this._dealerTheme = value;
  }
  public get ispDealerName(): string {
    return this._ispDealerName;
  }
  public set ispDealerName(value: string) {
    this._ispDealerName = value;
  }
  public get dealerRegionName(): string {
    return this._dealerRegionName;
  }
  public set dealerRegionName(value: string) {
    this._dealerRegionName = value;
  }
  public get dealerName(): string {
    return this._dealerName;
  }
  public set dealerName(value: string) {
    this._dealerName = value;
  }
    private _message: string; 
    showMessage: boolean = false;
  public get message(): string {
    return this._message;
  }
  public set message(value: string) {
    this._message = value;
  }
    public get dealerCode(): string {
        return this._dealerCode;
    }
    public set dealerCode(value: string) {
        this._dealerCode = value;
    }
 public headerObj: any ;
 constructor(private ispAppConfigService: ISPAppConfigService,private dataService: DataSharingService,
  translate: TranslateService,
private router: Router,private route: ActivatedRoute, private renderer: Renderer2) {
    this._dealerCode = '31069';
    this._message =  'Not a valid Dealer Code!';
    translate.addLangs(['en']);
    // this language will be used as a fallback when a translation isn't found in the current language
    translate.setDefaultLang('en');
    // the lang to use, if the lang isn't available, it will use the current loader to get them
   translate.use('en');
    this.headerObj = this.ispAppConfigService.getHeaderDetails();
      }

 
  ngOnInit() { 
    this.renderer.addClass(document.body, 'login');
    this.dataService.currentDealerCode.subscribe(value => this.dealerCode = value);
  }
  ngOnDestroy(){
    this.renderer.removeClass(document.body, 'login');
  }
  onSubmit(f: NgForm) {
    this._dealerName = "SEWELL LEXUS";
    this._dealerRegionName = "KANSAS CITY REGION";
    this._dealerTheme = "Lexus";
    let pattern = new RegExp("[0-9][0-9][0-9][0-9][0-9]");	
    if (pattern.test(this._dealerCode)) {
      if(this._ispDealerName == undefined){
        return;
      }
      let d = new Date();
      d.setTime(d.getTime() + (1*24*60*60*1000));
      let expires = "expires="+ d.toUTCString();
      document.cookie = "DealerDaily=SelectedDealer=" + this._dealerCode + "&" 
                      + "SelectedDealerName=" + this._dealerName + "&" 
                      + "SelectedTheme=" + this._dealerTheme + "&"
                      + "SelectedRegionName=" + this._dealerRegionName + "&" 
       + expires + ";path=/" ;
       this.dataService.changeMessage(this.dealerCode);
      // this.ispAppConfigService.load();      
      this.router.navigate(['vinSummary']);   
        setTimeout(() => {
          window.location.reload(); 
        }, 500);
      
      return;
    }
    else{
      this.showMessage = true;
      setTimeout( () => {
        this.showMessage = !this.showMessage;
      }, 2000);
  
    }
  }
}