import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { LoaderService } from './ip-loader.service';
import { LoaderState } from './ip-loader';

@Component({
    selector: 'app-ip-loader',
    templateUrl: 'ip-loader.component.html',
    styleUrls: ['ip-loader.component.scss']
})
export class ipLoaderComponent implements OnInit, OnDestroy {

    show = false;
    private subscription: Subscription;
    constructor(private loaderService: LoaderService) { }

    ngOnInit() {
        this.subscription = this.loaderService.loaderState
            .subscribe((state: LoaderState) => {
                this.show = state.show;
            });
    }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }
}
