

// request object used to fetch data in Payments page with form details
export class DealReviewSearchRequest {
    sortData: any[];
    filterData: filterParameter[];
    queryParams: DealReviewQueryParameter;
    currentPageNumber: number;
    excelUrl: {
        url: string | null
    };
    pageNumber: number;
    taskToDo: string;
    totalResults: number;
    pageCnt: string;
    totalPages: number;    
    recordsPerPage: number;
    // for controlling the number of data to be fetched
    showAll: boolean;

    // default values of few properties corrosponds to the pre-selected radio buttons on default page load
    constructor() {
        this.sortData = [];
        this.filterData = [];
        this.queryParams = new DealReviewQueryParameter();
        this.taskToDo = 'searchByUpdates';
        this.excelUrl = {
            url: "excelExporter/searchdealRvwSummary"
        };
        this.pageNumber = null;
        this.totalResults = null;
        this.showAll = false;
        this.pageCnt = '';
        this.totalPages = null;
        this.currentPageNumber = null;
        this.recordsPerPage = null;
    }
}

export class filterParameter {
    filterName: string;
    filterValue: string;
    constructor() {
        this.filterName = null;
        this.filterValue = null;
    }
}

// forms the query parameters for the above class in Payments page
export class DealReviewQueryParameter {
    dealerCode: string | null;
    searchByRadio: string | null;
    serialNo: string | null;
    status: string | null;
    vinSerial: string | null;
    vin: string | null;

    constructor() {
        this.dealerCode = '';
        this.searchByRadio = 'newUpdates';
        this.serialNo = '';
        this.status = '';
        this.vinSerial = '';
        this.vin = '';
    }
}