import { Injectable } from '@angular/core';
import * as FileSaver from 'file-saver';
import * as js2xmlparser from 'js2xmlparser';

@Injectable({
  providedIn: 'root'
})
export class XMLExportService {
  constructor() {}

  generateXML(excelVSData){    
    let xml = js2xmlparser.parse("VinSummaryExport",{ "Row": excelVSData });    
    const test = new Blob([xml], { type: 'text/plain' });    
    FileSaver(test, 'VinSummary.xml');
    return;    
   }
}
