import { FilterParameter } from './vinSummarySearchRequest';

export class PaymentDetailsRequest {
  sortData: any[];
  filterData: FilterParameter[];
  queryParams: PayDetailQueryParameter;
  excelUrl: {
    url: string | null
  };
  pageNumber: number;
  taskToDo: string;
  totalResults: number;
  pageCnt: string;
  totalPages: number;
  currentPageNumber: number;
  recordsPerPage: number;
  // for controlling the number of data to be fetched
  showAll: boolean;

  constructor() {
    this.sortData = [];
    this.filterData = [];
    this.queryParams = new PayDetailQueryParameter();
    this.excelUrl = {
      url: "excelExporter/searchPaymentsByMethodDetails"
    };
    this.pageNumber = 1;
    this.taskToDo = '';
    this.totalResults = null;
    this.showAll = false;
    this.pageCnt = '';
    this.totalPages = null;
    this.currentPageNumber = null;
    this.recordsPerPage = null;
  }
}

export class PayDetailQueryParameter {
  dealerCode: string;
  paymentReferenceNo: string;
  constructor() {
    this.dealerCode = '';
    this.paymentReferenceNo = '';
  }
}