import { FilterParameter } from './challengeSearchRequest';

// request object used to fetch data in Payments page with form details
export class PaymentsSearchRequest {
    sortData: any[];
    filterData: FilterParameter[];
    queryParams: PaymentSearchQueryParameter;
    excelUrl: {
        url: string | null
    };
    pageNumber: number;
    taskToDo: string;
    totalResults: number;
    pageCnt: string;
    totalPages: number;
    currentPageNumber: number;
    recordsPerPage: number;
    // for controlling the number of data to be fetched
    showAll: boolean;
    // required to distinguish between the different search options as both date dropdown and datepicker have same taskToDo value
    searchOption: string | null;

    // default values of few properties corrosponds to the pre-selected radio buttons on default page load
    constructor() {
        this.sortData = [];
        this.filterData = [];
        this.queryParams = new PaymentSearchQueryParameter();
        this.taskToDo = 'searchByDateRange';
        this.excelUrl = {
            url: ''
        };
        this.pageNumber = 1;
        this.totalResults = null;
        this.showAll = false;
        this.pageCnt = '';
        this.totalPages = null;
        this.currentPageNumber = null;
        this.recordsPerPage = null;
    }
}


// forms the query parameters for the above class in Payments page
export class PaymentSearchQueryParameter {
    dealerCode: string;
    paymentReferenceNo: string;
    startDate: string;
    endDate: string;
    month: string;
    year: number;

    constructor() {
        this.dealerCode = '';
        this.paymentReferenceNo = '';
        this.startDate = this.replaceNonAscii(new Date().toLocaleDateString());
        this.endDate = this.replaceNonAscii(new Date().toLocaleDateString());
        // set to current month in string, TBD later, mb
        this.month = '';
        this.year = new Date().getFullYear();
    }

    public replaceNonAscii(val) {
        return val.replace(/[^A-Za-z 0-9 \.,\?""!@#\$%\^&\*\(\)-_=\+;:<>\/\\\|\}\{\[\]`~]*/g, '');
    }
}
