export class CreateChallengeDetailsRequest {
    queryParams: QueryParameter;
    constructor() {
        this.queryParams = new QueryParameter();
    }

}
export class QueryParameter {
    vin: string;
    dealerCode: string;
    incentiveTypeNo: string;
    optionType: string;
    iaId: string;

    constructor() {
        this.vin = '';
        this.dealerCode = '';
        this.incentiveTypeNo = '';
        this.optionType = '';
        this.iaId = '';

    }

}
export class DeleteChallengeDetailsRequest {
    queryParams: deleteQueryParameter;
    constructor() {
        this.queryParams = new deleteQueryParameter();
    }
}
export class deleteQueryParameter {
    challengeNo: number;
    constructor() {
        this.challengeNo = null;
    }
}