import { Component, OnInit } from '@angular/core';
import { ISPAppConfigService } from '../services/isp-app-config.service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {
  public isLexus: boolean;
  public currentYear: String;
  constructor(private ispAppConfig: ISPAppConfigService) {
    if(this.ispAppConfig.getSelectedTheme() === 'Lexus'){
      this.isLexus = true;
    }
    var d = new Date();
    this.currentYear = d.getFullYear().toString().substring(2);

  }

  ngOnInit() {
  }

}
