// request object used to fetch data in VIN Summary page with form details
export class VSSearchRequest {
    sortData: any[];
    filterData: FilterParameter[];
    queryParams: VSSearchQueryParameter;
    excelUrl: {
        url: string | null
    };
    pageNumber: string;
    taskToDo: string;
    totalResults: number;
    pageCnt: string;
    totalPages: number;
    currentPageNumber: number;
    recordsPerPage: number;
    // for controlling the number of data to be fetched
    showAll: boolean;
    // default values of few properties corrosponds to the pre-selected radio buttons on default page load
    constructor() {
        this.sortData = [];
        this.filterData = [];
        this.queryParams = new VSSearchQueryParameter();
        this.taskToDo = 'searchByCalMonth';
        this.excelUrl = {
            url: 'excelExporter/vinSummary'
          };
        this.pageNumber = '1';
        this.totalResults = null;
        this.showAll = false;
        this.pageCnt = '';
        this.totalPages = null;
        this.currentPageNumber = null;
        this.recordsPerPage = null;
    }
}

// forms the base for object arrays required for the filterData property in the request object
export class FilterParameter {
    filterName: string;
    filterValue: string;
    constructor() {
        this.filterName = null;
        this.filterValue = null;
    }
}

// forms the query parameters for the above class in VIN Summary page
export class VSSearchQueryParameter {
    dealerCode: string;
    searchByIncentive: string;
    startDate: string;
    endDate: string;
    month: string;
    year: number;
    serialNo: string;
    vinSerial: string;
    vin: string;
    constructor() {
        this.dealerCode = '';
        this.searchByIncentive = 'Y';
        this.startDate = this.replaceNonAscii(new Date().toLocaleDateString());
        this.endDate = this.replaceNonAscii(new Date().toLocaleDateString());
        // set to current month in string, TBD later, mb
        this.month = '';
        this.year = new Date().getFullYear();
        this.serialNo = '';
        this.vinSerial = '';
        this.vin = '';
    }

    public replaceNonAscii(val) {
        return val.replace(/[^A-Za-z 0-9 \.,\?""!@#\$%\^&\*\(\)-_=\+;:<>\/\\\|\}\{\[\]`~]*/g, '');
      }
}
