// request object used to fetch data in Payments page with form details
export class IncentiveSearchRequest {
    sortData: any[];
    filterData: filterParameter[];
    queryParams: IncentiveSearchQueryParameter;
    excelUrl: {
        url: string | null
    };
    pageNumber: string;
    taskToDo: string;
    totalResults: number;
    pageCnt: string;
    totalPages: number;
    currentPageNumber: number;
    recordsPerPage: number;
    // for controlling the number of data to be fetched
    showAll: boolean;
    // default values of few properties corrosponds to the pre-selected radio buttons on default page load
    constructor() {
        this.sortData = [];
        this.filterData = [];
        this.queryParams = new IncentiveSearchQueryParameter();
        this.taskToDo = 'searchByIncentiveType';
        this.excelUrl = {
            url: 'excelExporter/searchPaymentsByType'
        };
        this.pageNumber = '1';
        this.totalResults = null;
        this.showAll = false;
        this.pageCnt = '';
        this.totalPages = null;
        this.currentPageNumber = null;
        this.recordsPerPage = null;
    }
}

// forms the base for object arrays required for the filterData property in the request object
export class filterParameter {
    filterName: string;
    filterValue: string;
    constructor() {
        this.filterName = null;
        this.filterValue = null;
    }
}

// forms the query parameters for the above class in Payments page
export class IncentiveSearchQueryParameter {
    dealerCode: string;
    // the below two are specifically for Payments by Incentive Reuqest
    incentiveMonth: string;
    incentiveYear: number;
    // the below two are specifically for Payments by Payments Reuqest
    constructor() {
        this.dealerCode = '';
        this.incentiveMonth = '';
        this.incentiveYear = new Date().getFullYear();
    }
}
